.modal-auth {
    position: fixed; /*   */
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.5); /*   */
    z-index: 1050;
    opacity: 0; /*      */    
    transition: opacity 200ms ease-in; /*   */
    pointer-events: none; /*      */
    margin: 0;
    padding: 0;
    font-family: Roboto;
}
/*     */
.modal-auth:target {
    opacity: 1; /*    */
	pointer-events: auto; /*      */
    overflow-y: auto; /*    y,       */
}
/*         */
.modal-auth__dialog {
    position: relative;
    width: auto;
    margin: 10px;
}
@media (min-width: 576px) {
  .modal-dialog {
      max-width: 500px;
      margin: 30px auto; /*       */
  }
}
/*   ,     */ 
.modal-auth__content {
    position: relative;    
    display: flex;    
    flex-direction: column;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: .3rem;
    outline: 0;
}
@media (min-width: 768px) {
  .modal-auth__content {      
      box-shadow: 0 5px 15px rgba(0,0,0,.5);
  }
}
/*      */
.modal-auth__header {    
    display: flex;    
    align-items: center;    
    justify-content: space-between;
    padding: 15px;
    border-bottom: 1px solid #eceeef;
}
.modal-auth__title {
    margin-top: 0;
    margin-bottom: 0;
    line-height: 1.5;
    font-size: 1.25rem;
    font-weight: 500;
}
/*    "" */
.modal-auth__close {
    float: right;
    font-family: sans-serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: .5;
    text-decoration: none;
}
/*    ""        */
.modal-auth__close:focus, .modal-auth__close:hover {
    color: #000;
    text-decoration: none;
    cursor: pointer;
    opacity: .75;
}
/*   ,     */
.modal-auth__body {
  position: relative;
  flex: 1 1 auto;
    padding: 15px;
    overflow: auto;
}

.modal-auth__btn-open {
    position: fixed;
    left: 100px;
    top: 100px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: transparent;
    font-family: Roboto;
    text-decoration: none;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.is-hidden-auth {
    display: none;
}